
.card {
    background: #fff;
    border-radius: 2px;
    height: 240px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 270px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: hidden;
  }

.hover img {
	margin-left: -10px;
	-webkit-transform: scale(1.5);
	-ms-transform: scale(1.5);
	    transform: scale(1.5);
	-webkit-transition: .3s ease-in-out;
	-o-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}
.hover img:hover  {
	margin-left: 110px;
}


